import React from 'react';
import '../../css/About.css';
import { Row, Col } from 'antd';
import TitleComponent from '../TitleComponent';


const About = props => {
    const abilities = {
        Python: '90%',
        PyTorch: '80%',
        Tensorflow: '70%',
        'C & C++': '50%'
    }
    const tags = [
        'Deep Learning', 'Natural Language Processing', 'Computer Vision', 'Machine Transition',
        'Image Transition', 'Sign Language Processing', 'Human Pose Estimation', 'Optical Character Recognition', 'Document Recognition'
    ]
    return (
        <section id="about">
            <div className="about container flex">
                <TitleComponent title="about"/>
                <Row className="about-characteristics-container">
                    <Col span="6" className="about-characteristics-item">
                        <img className="about-characteristics-item-img trigger" trigger="flipInX"
                            src="https://cdn.pixabay.com/photo/2017/05/13/23/05/emoji-2310895_960_720.png" />
                        <div className="about-characteristics-item-text-opacity-0 trigger" trigger="fadeIn">
                            <div className="about-characteristics-item-text">
                                FAST
                            </div>
                            <div className="about-characteristics-item-subtext">
                                Fast load times and lag free interaction, my highest priority.
                        </div>
                        </div>
                    </Col>
                    <Col span="6" className="about-characteristics-item">
                        <img className="about-characteristics-item-img delay1 trigger" trigger="flipInX" src="https://cdn.pixabay.com/photo/2017/05/13/23/05/emoji-2310895_960_720.png" />
                        <div className="delay1 trigger about-characteristics-item-text-opacity-0" trigger="fadeIn">
                            <div className="about-characteristics-item-text">
                                Responsive
                        </div>
                            <div className="about-characteristics-item-subtext">
                                My layouts will work on any device, big or small.
                        </div>
                        </div>
                    </Col>
                    <Col span="6" className="about-characteristics-item">
                        <img className="about-characteristics-item-img delay2 trigger" trigger="flipInX" src="https://cdn.pixabay.com/photo/2017/05/13/23/05/emoji-2310895_960_720.png" />
                        <div className="delay2 trigger about-characteristics-item-text-opacity-0" trigger="fadeIn">
                            <div className="about-characteristics-item-text">
                                Intuitive
                        </div>
                            <div className="about-characteristics-item-subtext">
                                Strong preference for easy to use, intuitive UX/UI.
                        </div>
                        </div>
                    </Col>
                    <Col span="6" className="about-characteristics-item">
                        <img className="about-characteristics-item-img delay3 trigger" trigger="flipInX" src="https://cdn.pixabay.com/photo/2017/05/13/23/05/emoji-2310895_960_720.png" />
                        <div className="delay3 trigger about-characteristics-item-text-opacity-0">
                            <div className="about-characteristics-item-text">
                                Dynamic
                        </div>
                            <div className="about-characteristics-item-subtext">
                                Websites don't have to be static, I love making pages come to life.
                            </div>
                        </div>
                    </Col>
                </Row>
                <Row className="about-profile-container" gutter={[32, 0]}>
                    <Col span="12" className="trigger" trigger="slide-in-left">
                        <img className="about-profile-img" src="https://cdn.pixabay.com/photo/2017/05/13/23/05/emoji-2310895_960_720.png" />
                        <div>
                            Who's this guy?
                            </div>
                        <div>
                            I'm a Front-End Developer for <span style={{color:'#FF8D00'}}>ChowNow</span> in Los Angeles, CA.<br/>
                            I have serious passion for UI effects, animations and creating<br/>intuitive, dynamic user experiences.<br/>
                            <span style={{color:'#FF8D00'}}>Let's make something special.</span>
                            </div>
                    </Col>
                    <Col span="12" className="about-ability-container trigger" trigger="slide-in-right">
                        {Object.keys(abilities).map((ab,ind) => {
                            return <div className="ability flex" key={ab + ind}>
                                <div className="ability-bar" style={{width:abilities[ab]}}>
                                    <div className="ability-name flex">
                                        {ab}
                                    </div>
                                </div>
                                <span className="ability-percentage">
                                    {abilities[ab]}
                            </span>
                            </div>
                        })}
                        {tags.map((tag,ind) => {
                            return <div className="ability-tag" key={tag + ind}>{tag}</div>
                        })}
                    </Col>
                </Row>
            </div>
        </section>
    )
}

export default About;